                    {{ tab.value }}
                    <!-- ‰∏∫È¢ÑÁ∫¶‰∏≠tabÊ∑ªÂä†uni-badge -->
                    <uni-badge v-if="tab.value === 'ÂæÖÂ§ÑÁêÜ' && bookingBadgeText !== 0" class="uni-badge" type="error"
                        :text="bookingBadgeText" :is-dot="false" absolute="rightTop" :offset="[-5, -12]"></uni-badge>

                    <view class="tab-line" v-if="currentTab === index"></view>
                </view>
            </view>
        </view>

        <!-- ÂÜÖÂÆπÂå∫Âüü -->
        <view class="content-wrapper">
            <scroll-view class="content" scroll-y>
                <!-- Êï∞ÊçÆÂàóË°® -->
                <view class="order-list" v-if="allOrderList.length > 0">
                    <view class="order-item" v-for="(item, index) in allOrderList" :key="index">
                        <view class="order-header">
                            <view class="shop-info">
                                <text class="shop-name">{{ item.merchantName }}</text>
                                <text class="status-tag" :class="getStatusClass(item.status)">
                                    {{ getStatusText(item.status) }}
                                </text>
                            </view>
                        </view>
                        <view class="order-content">
                            <view class="info-item">
                                <text class="label">È¢Ñ‰º∞Êó∂Èó¥:</text>
                                <text class="value">{{ item.appointmentTime ?? 'ÊöÇÊó†' }}</text>
                            </view>
                            <view class="info-item">
                                <text class="label">Êî∂ËøêÊó∂Èó¥:</text>
                                <text class="value">{{ item.arrivalTime ?? 'ÊöÇÊó†' }}</text>
                            </view>
                            <view class="info-item">
                                <text class="label">È¢Ñ‰º∞ÈáçÈáè:</text>
                                <text class="value">{{ item.estimateWeight ? (item.estimateWeight + 'kg') : 'ÊöÇÊó†'
                                }}</text>
                            </view>
                            <view class="info-item">
                                <text class="label">Êî∂ËøêÈáçÈáè:</text>
                                <text class="value">{{ item.weight ? (item.weight + 'kg') : 'ÊöÇÊó†' }}</text>
                            </view>
                            <view class="info-item">
                                <text class="label">È¢Ñ‰º∞Ê°∂Êï∞:</text>
                                <text class="value">{{ item.estimateBucketNum ? (item.estimateBucketNum + '‰∏™') : 'ÊöÇÊó†'
                                }}</text>
                            </view>
                            <view class="info-item">
                                <text class="label">Êî∂ËøêÊ°∂Êï∞:</text>
                                <text class="value">{{ item.bucketNum ? (item.bucketNum + '‰∏™') : 'ÊöÇÊó†' }} </text>
                            </view>
                            <view class="info-item">
                                <text class="label">Âú∞ÂùÄ:</text>
                                <text class="value">{{ item.address ?? 'ÊöÇÊó†' }} </text>
                            </view>

                        </view>
                        <view class="order-footer">
                            <template v-if="item.status == 0 || item.status == '0'">
                                <uni-button size="mini" type="default" class="cancel-btn" @tap="handleCancel(item)">
                                    ÂèñÊ∂à
                                </uni-button>
                                <uni-button size="mini" type="primary" class="report-btn"
                                    @tap="handleConfirmTransport(item)">
                                    Êî∂Ëøê‰∏äÊä•
                                </uni-button>
                            </template>
                            <template v-else>
                                <uni-button size="mini" type="default" class="view-btn" @tap="handleViewDetails(item)">
                                    Êü•ÁúãËØ¶ÊÉÖ
                                </uni-button>
                            </template>
                        </view>
                    </view>

                    <!-- Âä†ËΩΩÊõ¥Â§öÁªÑ‰ª∂ - Âè™Âú®ÊúâÊï∞ÊçÆÊó∂ÊòæÁ§∫ -->
                    <view class="loadMore">
                        <uni-load-more :status="loadingStatus" :content-text="{
                            contentdown: '‰∏äÊãâÊòæÁ§∫Êõ¥Â§ö',
                            contentrefresh: 'Ê≠£Âú®Âä†ËΩΩ...',
                            contentnomore: 'Ê≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ‰∫Ü'
                        }"></uni-load-more>
                    </view>
                </view>

                <!-- ÊöÇÊó†Êï∞ÊçÆÁä∂ÊÄÅ -->
                <view class="empty-state" v-else-if="loadingStatus !== 'loading'">
                    <view class="empty-icon">üìã</view>
                    <text class="empty-text">ÊöÇÊó†Êï∞ÊçÆ</text>
                    <text class="empty-desc">ÂΩìÂâçÂàÜÁ±ª‰∏ãÊöÇÊó∂Ê≤°ÊúâÁõ∏ÂÖ≥‰ø°ÊÅØ</text>
                </view>
            </scroll-view>
        </view>
    </view>
</template>
<script setup>
import {
    ref,
    computed,
    onMounted
} from 'vue';
import {
    onPullDownRefresh,
    onReachBottom
} from '@dcloudio/uni-app';

import {
    apiGetDriverPlanPage, apiGetnoNeedCollect, apiGetdriverConfirmPlan, apiGetDriverNotConfirmNum
} from '@/api/apis.js';

import { onShow } from '@dcloudio/uni-app' // ÂØºÂÖ•onShowÁîüÂëΩÂë®Êúü

import { useUserStore } from '@/stores/user.js'


const tabs = [{ key: "3", value: "ÂéÜÂè≤ËÆ∞ÂΩï" }, { key: "0", value: "ÂæÖÂ§ÑÁêÜ" }, { key: "1", value: "Â∑≤ÂÆåÊàê" }];
//‰∏ãÊ†á
const currentTab = ref(0);

// ÂΩìÂâçÈÄâ‰∏≠tabÁöÑstatusÂÄºÔºàËΩ¨Êç¢‰∏∫Êï¥Êï∞Ôºâ
const currentStatusKey = computed(() => parseInt(tabs[currentTab.value].key));

// Á≠õÈÄâÁõ∏ÂÖ≥Áä∂ÊÄÅ
const searchKeyword = ref(''); // ÊêúÁ¥¢ÂÖ≥ÈîÆËØç

const userStore = useUserStore();

//ËøîÂõû‰∏ä‰∏ÄÈ°µ
const back = () => {
    uni.navigateBack()
}

//ÁÇπÂáªËèúÂçï
function handleTabClick(index) {
    currentTab.value = index;
    allOrderList.value = [];
    pageNum.value = 1; // ÈáçÁΩÆÈ°µÁ†Å‰∏∫1
    getNetwork();
}

// È°µÈù¢ÊòæÁ§∫Êó∂Âà∑Êñ∞Êï∞ÊçÆ
onShow(async () => {
    console.log('È°µÈù¢ÊòæÁ§∫Êó∂Âà∑Êñ∞Êï∞ÊçÆ')
    getDriverNotConfirmNum();
    getNetwork();
})


// Áä∂ÊÄÅËΩ¨Êç¢ÂáΩÊï∞
const getStatusText = (status) => {
    switch (status) {
        case 0:
        case '0':
            return 'ËøõË°å‰∏≠';
        case 1:
        case '1':
            return 'Â∑≤ÂÆåÊàê';
        case 2:
        case '2':
            return 'Êó†Ê≥ïÊî∂Ëøê';
        default:
            return 'Êó†Ê≥ïÊî∂Ëøê';
    }
};

// Ëé∑ÂèñÁä∂ÊÄÅÊ†∑ÂºèÁ±ªÂêç
const getStatusClass = (status) => {
    switch (status) {
        case 0: return 'processing';
        case 1: return 'completed';
        case 2: return 'cancelled';
    }
};

// ÊåâÈíÆÁÇπÂáª‰∫ã‰ª∂Â§ÑÁêÜÂáΩÊï∞
const handleCancel = (item) => {
    console.log('ÂèñÊ∂à‰ªªÂä°:', item);
    uni.showModal({
        title: 'Á°ÆËÆ§ÂèñÊ∂à',
        content: 'ÊòØÂê¶Á°ÆËÆ§ÂèñÊ∂àÂΩìÂâç‰ªªÂä°Ôºü',
        success: async (res) => {
            if (res.confirm) {
                await apiGetnoNeedCollect({
                    id: item.id,
                    driverId: userStore.sfmerchant?.id
                }).then((res) => {
                    if (res.code === 200) {
                        uni.showToast({
                            title: res.msg || 'Êìç‰ΩúÊàêÂäü',
                            icon: 'success'
                        });
                        // Âà∑Êñ∞‰ªªÂä°ÂàóË°®
                        clearSearch();
                    } else {
                        uni.showToast({
                            title: res.msg || 'Êìç‰ΩúÂ§±Ë¥•',
                            icon: 'error'
                        });
                    }

                })
            }
        }
    })

};

const handleViewDetails = (item) => {
    console.log('Êü•ÁúãËØ¶ÊÉÖÊåâÈíÆË¢´ÁÇπÂáª', item);
    // ËøôÈáåÊ∑ªÂä†Êü•Áúã‰ªªÂä°ÁöÑÈÄªËæë
    uni.navigateTo({
        url: `/pages/collection/syCheckDetail?planId=${item.id}&driverId=${item.driverId}`
    });

};

const handleConfirmTransport = async (task) => {
    console.log('Êî∂Ëøê:', task.id);
    //ÂÖàÂà§Êñ≠task.weightÊòØÂê¶Â§ß‰∫é0 he task.bucketNumÊòØÂê¶Â§ß‰∫é0
    if (task.weight > 0 && task.bucketNum > 0) {
        //Á°ÆËÆ§Êî∂ËøêÂÆåÊàê
        uni.showModal({
            title: 'Á°ÆËÆ§Êî∂ËøêÂÆåÊàê',
            content: 'ÊòØÂê¶Á°ÆËÆ§Êî∂ËøêÂÆåÊàêÔºü',
            success: async (res) => {
                if (res.confirm) {
                    await apiGetdriverConfirmPlan({
                        id: task.id,
                        driverId: userStore.sfmerchant?.id,
                    }).then((res) => {
                        if (res.code === 200) {
                            uni.showToast({
                                title: res.msg || 'Êìç‰ΩúÊàêÂäü',
                                icon: 'success'
                            });
                            // Âà∑Êñ∞‰ªªÂä°ÂàóË°®
                            clearSearch();
                        } else {
                            uni.showToast({
                                title: res.msg || 'Êìç‰ΩúÂ§±Ë¥•',
                                icon: 'error'
                            });
                        }
                    })
                }
            }
        })

    }
    else {
        uni.showToast({
            title: 'ËØ∑ÂÖàËøõË°å Êî∂Ëøê‰∏äÊä• Êìç‰Ωú',
            icon: 'none'
        });
        return;
    }
};

// Ê∑ªÂä†badgeTextÁöÑrefÂèòÈáè
const bookingBadgeText = ref(0);

const getDriverNotConfirmNum = async () => {
    const res = await apiGetDriverNotConfirmNum({
        driverId: userStore.sfmerchant?.id
    });
    if (res.code === 200) {
        bookingBadgeText.value = res.data ?? 0;
    }
};


// Ê∑ªÂä†È°µÁ†ÅÂíåÂä†ËΩΩÁä∂ÊÄÅÂèòÈáè
const pageNum = ref(1);
const loadingStatus = ref('more'); // more-Âä†ËΩΩÂâç/loading-Âä†ËΩΩ‰∏≠/nomore-Ê≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ

// Êï∞ÊçÆÂàóË°®
const allOrderList = ref([]);

//ÂêéËé∑ÂèñÊï∞ÊçÆ
const getNetwork = async () => {
    try {
        // Â¶ÇÊûú‰∏çÊòØÁ¨¨‰∏ÄÈ°µÔºåËÆæÁΩÆÂä†ËΩΩÁä∂ÊÄÅ‰∏∫Âä†ËΩΩ‰∏≠
        if (pageNum.value > 1) {
            loadingStatus.value = 'loading';
        }

        // ÊûÑÂª∫ËØ∑Ê±ÇÂèÇÊï∞
        const params = {
            pageNum: pageNum.value,
            driverId: userStore.sfmerchant?.id,
            status: currentStatusKey.value // ‰ΩøÁî®ÂΩìÂâçÈÄâ‰∏≠tabÂØπÂ∫îÁöÑÊï¥Êï∞Á±ªÂûã
        };

        // Ê∑ªÂä†ÊêúÁ¥¢ÂÖ≥ÈîÆËØç
        if (searchKeyword.value) {
            params.title = searchKeyword.value;
        }

        const res = await apiGetDriverPlanPage(params);


        // Â§ÑÁêÜ‰∏ãÊãâÂà∑Êñ∞
        if (pageNum.value === 1) {
            allOrderList.value = res.data.list || [];
            uni.stopPullDownRefresh();
        } else {
            // Â§ÑÁêÜ‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö
            allOrderList.value = [...allOrderList.value, ...(res.data.list || [])];
        }

        // Âà§Êñ≠ÊòØÂê¶ËøòÊúâÊõ¥Â§öÊï∞ÊçÆ
        if (res.data.list && res.data.list.length < 10) {
            // Â¶ÇÊûúËøîÂõûÁöÑÊï∞ÊçÆÂ∞ë‰∫éÊØèÈ°µÊï∞ÈáèÔºåËØ¥ÊòéÊ≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ‰∫Ü
            loadingStatus.value = 'nomore';
        } else {
            // Âê¶ÂàôËøòÊúâÊõ¥Â§öÊï∞ÊçÆ
            loadingStatus.value = 'more';
        }
    } catch (error) {
        console.error('Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•:', error);

        // ÂÅúÊ≠¢‰∏ãÊãâÂà∑Êñ∞
        uni.stopPullDownRefresh();

        // ÈáçÁΩÆÂä†ËΩΩÁä∂ÊÄÅ
        loadingStatus.value = 'more';

        // Â¶ÇÊûúÊòØÁ¨¨‰∏ÄÈ°µÂä†ËΩΩÂ§±Ë¥•ÔºåÁ°Æ‰øùÊòæÁ§∫ÊöÇÊó†Êï∞ÊçÆÁä∂ÊÄÅ
        if (pageNum.value === 1) {
            allOrderList.value = [];
        }

        uni.showToast({
            title: 'Êï∞ÊçÆÂä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
            icon: 'none',
            duration: 2000
        });
    }
};

// ÊêúÁ¥¢ÊñπÊ≥ï
const onSearch = () => {

    resetPageAndReload();
};



// Ê∏ÖÁ©∫ÊêúÁ¥¢ÂÖ≥ÈîÆËØç
const clearSearch = () => {
    searchKeyword.value = '';
    resetPageAndReload();
};

const resetPageAndReload = () => {
    console.log('ÈáçÁΩÆÈ°µÁ†ÅÂíåÈáçÊñ∞Âä†ËΩΩÊï∞ÊçÆ');
    allOrderList.value = [];
    pageNum.value = 1;
    getNetwork();

};


// ‰∏äÊãâÂä†ËΩΩÊõ¥Â§öÊñπÊ≥ï
const onLoadMore = () => {
    if (loadingStatus.value === 'nomore') return;
    // È°µÁ†ÅÂ¢ûÂä†
    pageNum.value++;
    getNetwork();
};


// Ëß¶Â∫ïÂä†ËΩΩÊõ¥Â§ö
onReachBottom(() => {
    onLoadMore();
});

//‰∏ãÊãâÂà∑Êñ∞
onPullDownRefresh(() => {
    allOrderList.value = [];
    pageNum.value = 1; // ÈáçÁΩÆÈ°µÁ†Å‰∏∫1
    getNetwork();
    getDriverNotConfirmNum()
})

// ÁªÑ‰ª∂ÊåÇËΩΩÊó∂ÂàùÂßãÂåñÊï∞ÊçÆ
onMounted(() => {
    pageNum.value = 1;
    getNetwork();
    getDriverNotConfirmNum()
});
</script>

